// pages/accounts.js
import { useState, useEffect, useMemo } from "react";
import Layout from "../components/Layout";
import Modal from "../components/Modal";
import toast from "react-hot-toast";
import theme from "../theme";
import {
  ResponsiveContainer,
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
} from "recharts";

export default function AccountsPage() {
  const [user] = useState({ name: "ÿßŸÑŸÖÿØŸäÿ± ÿ£ÿ≠ŸÖÿØ", role: "admin" });

  // üîπ ÿ®ŸäÿßŸÜÿßÿ™ ÿßŸÑÿπŸÖŸÑŸäÿßÿ™ ÿßŸÑŸÖÿßŸÑŸäÿ© (ÿ™ÿ¨ÿ±Ÿäÿ®Ÿäÿ© ÿ≠ÿßŸÑŸäÿßŸã)
  const [transactions, setTransactions] = useState([]);
  const [users, setUsers] = useState([]);

  // üîπ ŸÅŸÑÿßÿ™ÿ±
  const [search, setSearch] = useState("");
  const [typeFilter, setTypeFilter] = useState("all");
  const [directionFilter, setDirectionFilter] = useState("all");
  const [userFilter, setUserFilter] = useState("all");
  const [dateFrom, setDateFrom] = useState("");
  const [dateTo, setDateTo] = useState("");

  // üîπ ŸÖŸàÿØÿßŸÑ ÿ•ÿ∂ÿßŸÅÿ© ÿπŸÖŸÑŸäÿ©
  const [showAddModal, setShowAddModal] = useState(false);
  const [newTrans, setNewTrans] = useState({
    userId: "",
    type: "ŸÅÿßÿ™Ÿàÿ±ÿ© ÿ®Ÿäÿπ",
    direction: "in",
    amount: "",
    date: "",
    description: "",
    category: "ŸÖÿ®Ÿäÿπÿßÿ™",
    paymentMethod: "cash",
    refCode: "",
  });

  // ÿ™ŸÜÿ≥ŸäŸÇ ÿπŸÖŸÑÿ©
  const formatCurrency = (v) =>
    `${Number(v || 0).toLocaleString("ar-SA")} ÿ±.ÿ≥`;

  // üìå ÿ™ÿ≠ŸÖŸäŸÑ ÿ®ŸäÿßŸÜÿßÿ™ ÿ™ÿ¨ÿ±Ÿäÿ®Ÿäÿ©
  useEffect(() => {
  loadTransactions();
  loadUsers();
}, []);

const loadTransactions = async () => {
  try {
    const res = await api.get("/transactions");
    setTransactions(res.data);
  } catch (err) {
    console.error(err);
    toast.error("ÿÆÿ∑ÿ£ ŸÅŸä ÿ™ÿ≠ŸÖŸäŸÑ ÿßŸÑÿπŸÖŸÑŸäÿßÿ™");
  }
};

const loadUsers = async () => {
  try {
    const res = await api.get("/users");
    setUsers(res.data);
  } catch (err) {
    console.error(err);
  }
};

  // üîç ŸÅŸÑÿ™ÿ±ÿ© ÿßŸÑÿπŸÖŸÑŸäÿßÿ™
  const filteredTransactions = useMemo(() => {
    return transactions.filter((t) => {
      const q = search.trim().toLowerCase();
      const passSearch =
        !q ||
        t.refCode?.toLowerCase().includes(q) ||
        t.description?.toLowerCase().includes(q) ||
        t.userName?.toLowerCase().includes(q);

      const passType = typeFilter === "all" || t.type === typeFilter;
      const passDir =
        directionFilter === "all" || t.direction === directionFilter;
      const passUser = userFilter === "all" || t.userId === userFilter;

      const d = t.date.slice(0, 10);
      const passFrom = !dateFrom || d >= dateFrom;
      const passTo = !dateTo || d <= dateTo;

      return passSearch && passType && passDir && passUser && passFrom && passTo;
    });
  }, [
    transactions,
    search,
    typeFilter,
    directionFilter,
    userFilter,
    dateFrom,
    dateTo,
  ]);

  // üìä ŸÖŸÑÿÆÿµÿßÿ™
  const summary = useMemo(() => {
    let income = 0;
    let expense = 0;

    filteredTransactions.forEach((t) => {
      if (t.direction === "in") income += t.amount || 0;
      else expense += t.amount || 0;
    });

    const net = income - expense;

    // "ÿßŸÑŸäŸàŸÖ" ÿßŸÑŸÖŸÜÿ∑ŸÇŸä = ÿ£ÿ≠ÿØÿ´ ÿ™ÿßÿ±ŸäÿÆ ŸÅŸä ÿßŸÑÿ®ŸäÿßŸÜÿßÿ™
    const dates = transactions.map((t) => t.date.slice(0, 10));
    const logicalToday = dates.length ? dates.sort()[dates.length - 1] : null;

    let todayIncome = 0;
    if (logicalToday) {
      transactions.forEach((t) => {
        if (t.direction === "in" && t.date.slice(0, 10) === logicalToday) {
          todayIncome += t.amount || 0;
        }
      });
    }

    return {
      income,
      expense,
      net,
      todayIncome,
      count: filteredTransactions.length,
    };
  }, [filteredTransactions, transactions]);

  // üìà ÿÆÿ∑ ÿ≤ŸÖŸÜŸä ÿ®ÿ≥Ÿäÿ∑
  const chartData = useMemo(() => {
    const map = new Map();
    filteredTransactions.forEach((t) => {
      const day = t.date.slice(0, 10);
      const sign = t.direction === "in" ? 1 : -1;
      map.set(day, (map.get(day) || 0) + sign * (t.amount || 0));
    });
    return Array.from(map.entries())
      .sort(([a], [b]) => (a > b ? 1 : -1))
      .map(([date, total]) => ({ date, total }));
  }, [filteredTransactions]);

  // üñ®Ô∏è ÿ∑ÿ®ÿßÿπÿ© ÿßŸÑÿ™ŸÇÿ±Ÿäÿ± ÿßŸÑÿ≠ÿßŸÑŸä
  const handlePrint = () => {
    const rows = filteredTransactions
      .map(
        (t) => `
      <tr>
        <td>${t.refCode || ""}</td>
        <td>${t.type}</td>
        <td>${t.direction === "in" ? "ÿ•Ÿäÿ±ÿßÿØ" : "ŸÖÿµÿ±ŸàŸÅ"}</td>
        <td>${t.userName || ""}</td>
        <td>${t.date.replace("T", " ").slice(0, 16)}</td>
        <td>${t.amount}</td>
        <td>${t.category || ""}</td>
        <td>${t.paymentMethod || ""}</td>
      </tr>`
      )
      .join("");

    const html = `
      <html dir="rtl" lang="ar">
        <head>
          <meta charset="UTF-8" />
          <title>ÿ™ŸÇÿ±Ÿäÿ± ÿßŸÑÿ≠ÿ≥ÿßÿ®ÿßÿ™</title>
          <style>
            body { font-family: 'Tajawal', sans-serif; padding: 20px; }
            h2 { color: #0ea5e9; }
            table { width: 100%; border-collapse: collapse; margin-top: 10px; }
            th, td { border: 1px solid #ddd; padding: 6px; text-align: center; font-size: 13px; }
            th { background: #f3f4f6; }
          </style>
        </head>
        <body>
          <h2>ÿ™ŸÇÿ±Ÿäÿ± ÿßŸÑÿ≠ÿ≥ÿßÿ®ÿßÿ™ ÿßŸÑŸÖÿßŸÑŸäÿ© ‚Äî ÿµŸäÿØŸÑŸäÿ© ÿßŸÑŸÖÿπŸÑŸÖ</h2>
          <p>ÿ•ÿ¨ŸÖÿßŸÑŸä ÿßŸÑÿ•Ÿäÿ±ÿßÿØÿßÿ™: <strong>${formatCurrency(
            summary.income
          )}</strong></p>
          <p>ÿ•ÿ¨ŸÖÿßŸÑŸä ÿßŸÑŸÖÿµÿ±ŸàŸÅÿßÿ™: <strong>${formatCurrency(
            summary.expense
          )}</strong></p>
          <p>ÿµÿßŸÅŸä ÿßŸÑÿ±ÿ®ÿ≠: <strong>${formatCurrency(summary.net)}</strong></p>
          <table>
            <thead>
              <tr>
                <th>ŸÖÿ±ÿ¨ÿπ</th>
                <th>ÿßŸÑŸÜŸàÿπ</th>
                <th>ÿßÿ™ÿ¨ÿßŸá</th>
                <th>ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖ</th>
                <th>ÿßŸÑÿ™ÿßÿ±ŸäÿÆ</th>
                <th>ÿßŸÑŸÖÿ®ŸÑÿ∫</th>
                <th>ÿßŸÑÿ™ÿµŸÜŸäŸÅ</th>
                <th>ÿ∑ÿ±ŸäŸÇÿ© ÿßŸÑÿØŸÅÿπ</th>
              </tr>
            </thead>
            <tbody>
              ${rows}
            </tbody>
          </table>
          <script>
            window.onload = () => {
              window.print();
              setTimeout(() => window.close(), 600);
            };
          </script>
        </body>
      </html>
    `;

    const w = window.open("", "_blank", "width=900,height=900");
    w.document.write(html);
    w.document.close();
  };

  // ‚ûï ÿ•ÿ∂ÿßŸÅÿ© ÿπŸÖŸÑŸäÿ© ŸÖÿßŸÑŸäÿ© ÿ¨ÿØŸäÿØÿ© (ÿØÿßÿÆŸÑ ÿßŸÑŸàÿßÿ¨Ÿáÿ© ŸÅŸÇÿ∑ ÿßŸÑÿ¢ŸÜ)
  const handleSaveNewTrans = () => {
    if (!newTrans.userId || !newTrans.amount || !newTrans.date) {
      toast.error("Ÿäÿ±ÿ¨Ÿâ ÿ•ÿØÿÆÿßŸÑ ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖ ŸàÿßŸÑŸÖÿ®ŸÑÿ∫ ŸàÿßŸÑÿ™ÿßÿ±ŸäÿÆ");
      return;
    }

    const userObj = users.find((u) => u.id === newTrans.userId);

    const trans = {
      id: `T-${Date.now()}`,
      refCode: newTrans.refCode || "",
      type: newTrans.type,
      direction: newTrans.direction,
      amount: Number(newTrans.amount),
      date: newTrans.date,
      userId: newTrans.userId,
      userName: userObj?.name || "",
      category: newTrans.category,
      paymentMethod: newTrans.paymentMethod,
      description: newTrans.description,
    };

    setTransactions((prev) => [trans, ...prev]);
    setShowAddModal(false);
    setNewTrans({
      userId: "",
      type: "ŸÅÿßÿ™Ÿàÿ±ÿ© ÿ®Ÿäÿπ",
      direction: "in",
      amount: "",
      date: "",
      description: "",
      category: "ŸÖÿ®Ÿäÿπÿßÿ™",
      paymentMethod: "cash",
      refCode: "",
    });

    toast.success("ÿ™ŸÖ ÿ•ÿ∂ÿßŸÅÿ© ÿßŸÑÿπŸÖŸÑŸäÿ© ŸÖÿ≠ŸÑŸäÿßŸã (ÿ®ŸäÿßŸÜÿßÿ™ ÿ™ÿ¨ÿ±Ÿäÿ®Ÿäÿ©)");
  };

  // ÿ™ÿ¨ŸÖŸäÿπ ÿ≠ÿ≥ÿ® ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖ (ÿØŸÅÿ™ÿ± ÿ®ÿ≥Ÿäÿ∑)
  const userLedger = useMemo(() => {
    const map = new Map();
    transactions.forEach((t) => {
      if (!t.userId) return;
      if (!map.has(t.userId)) {
        map.set(t.userId, {
          userId: t.userId,
          userName: t.userName || "",
          income: 0,
          expense: 0,
        });
      }
      const row = map.get(t.userId);
      if (t.direction === "in") row.income += t.amount || 0;
      else row.expense += t.amount || 0;
    });
    return Array.from(map.values());
  }, [transactions]);

  return (
    <Layout user={user} title="ÿßŸÑÿ≠ÿ≥ÿßÿ®ÿßÿ™ ÿßŸÑŸÖÿßŸÑŸäÿ©">
      <div dir="rtl" className="space-y-6">
        {/* ÿ±ÿ£ÿ≥ ÿßŸÑÿµŸÅÿ≠ÿ© */}
        <div className="flex flex-col gap-3 sm:flex-row sm:items-center sm:justify-between">
          <div>
            <h1 className="text-xl font-semibold text-gray-800">
              üí∞ ÿßŸÑÿ≠ÿ≥ÿßÿ®ÿßÿ™ ÿßŸÑŸÖÿßŸÑŸäÿ©
            </h1>
            <p className="text-sm text-gray-500">
              ŸÖÿ™ÿßÿ®ÿπÿ© ÿßŸÑÿ•Ÿäÿ±ÿßÿØÿßÿ™ÿå ÿßŸÑŸÖÿµÿ±ŸàŸÅÿßÿ™ÿå ŸàÿµÿßŸÅŸä ÿßŸÑÿ±ÿ®ÿ≠ ŸÖÿπ ÿßÿ±ÿ™ÿ®ÿßÿ∑ ÿ®ÿ≥Ÿäÿ∑ ÿ®ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖŸäŸÜ
            </p>
          </div>
          <div className="flex flex-wrap gap-2">
            <button
              onClick={() => setShowAddModal(true)}
              className="px-4 py-2 text-sm text-white rounded-md shadow-md"
              style={{ background: theme.colors.success }}
            >
              ‚ûï ÿ•ÿ∂ÿßŸÅÿ© ÿπŸÖŸÑŸäÿ© ŸÖÿßŸÑŸäÿ©
            </button>
            <button
              onClick={handlePrint}
              className="px-4 py-2 text-sm border rounded-md text-sky-700 bg-sky-50 border-sky-300 hover:bg-sky-100"
            >
              üñ®Ô∏è ÿ∑ÿ®ÿßÿπÿ© ÿßŸÑÿ™ŸÇÿ±Ÿäÿ± ÿßŸÑÿ≠ÿßŸÑŸä
            </button>
          </div>
        </div>

        {/* ÿßŸÑŸÉÿ±Ÿàÿ™ */}
        <div className="grid grid-cols-1 gap-4 sm:grid-cols-2 lg:grid-cols-4">
          <SummaryCard
            title="ÿ•ÿ¨ŸÖÿßŸÑŸä ÿßŸÑÿ•Ÿäÿ±ÿßÿØÿßÿ™"
            value={formatCurrency(summary.income)}
            color="text-emerald-600"
          />
          <SummaryCard
            title="ÿ•ÿ¨ŸÖÿßŸÑŸä ÿßŸÑŸÖÿµÿ±ŸàŸÅÿßÿ™"
            value={formatCurrency(summary.expense)}
            color="text-red-600"
          />
          <SummaryCard
            title="ÿµÿßŸÅŸä ÿßŸÑÿ±ÿ®ÿ≠"
            value={formatCurrency(summary.net)}
            color={
              summary.net >= 0 ? "text-sky-600" : "text-red-700"
            }
          />
          <SummaryCard
            title="ÿπÿØÿØ ÿßŸÑÿπŸÖŸÑŸäÿßÿ™ ÿßŸÑÿ≠ÿßŸÑŸäÿ©"
            value={summary.count}
            color="text-purple-600"
          />
        </div>

        {/* ÿßŸÑÿ±ÿ≥ŸÖ ÿßŸÑÿ®ŸäÿßŸÜŸä + ÿØŸÅÿ™ÿ± ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖŸäŸÜ */}
        <div className="grid grid-cols-1 gap-4 lg:grid-cols-3">
          <div className="p-4 bg-white border rounded-lg shadow-sm lg:col-span-2">
            <h2 className="mb-2 text-sm font-semibold text-gray-700">
              üìà ÿµÿßŸÅŸä ÿßŸÑÿ≠ÿ±ŸÉÿ© ÿßŸÑŸäŸàŸÖŸäÿ© (ÿ•Ÿäÿ±ÿßÿØ - ŸÖÿµÿ±ŸàŸÅ)
            </h2>
            <div className="h-64">
              <ResponsiveContainer width="100%" height="100%">
                <LineChart data={chartData}>
                  <CartesianGrid strokeDasharray="3 3" />
                  <XAxis dataKey="date" />
                  <YAxis />
                  <Tooltip />
                  <Line
                    type="monotone"
                    dataKey="total"
                    stroke={theme.colors.primary}
                    strokeWidth={2}
                  />
                </LineChart>
              </ResponsiveContainer>
            </div>
          </div>

          <div className="p-4 bg-white border rounded-lg shadow-sm">
            <h2 className="mb-2 text-sm font-semibold text-gray-700">
              üë• ŸÖŸÑÿÆÿµ ÿ≠ÿ≥ÿ® ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖ
            </h2>
            <div className="space-y-2 text-xs">
              {userLedger.length ? (
                userLedger.map((u) => (
                  <div
                    key={u.userId}
                    className="p-2 border rounded-md bg-gray-50/70"
                  >
                    <div className="flex items-center justify-between">
                      <span className="font-semibold text-gray-800">
                        {u.userName}
                      </span>
                      <span className="text-[11px] text-gray-500">
                        {u.userId}
                      </span>
                    </div>
                    <div className="mt-1 text-[12px]">
                      <div className="flex justify-between">
                        <span>ÿ•Ÿäÿ±ÿßÿØÿßÿ™</span>
                        <span className="font-semibold text-emerald-700">
                          {formatCurrency(u.income)}
                        </span>
                      </div>
                      <div className="flex justify-between">
                        <span>ŸÖÿµÿ±ŸàŸÅÿßÿ™</span>
                        <span className="font-semibold text-red-600">
                          {formatCurrency(u.expense)}
                        </span>
                      </div>
                      <div className="flex justify-between">
                        <span>ÿµÿßŸÅŸä</span>
                        <span
                          className={`font-bold ${
                            u.income - u.expense >= 0
                              ? "text-sky-600"
                              : "text-red-700"
                          }`}
                        >
                          {formatCurrency(u.income - u.expense)}
                        </span>
                      </div>
                    </div>
                  </div>
                ))
              ) : (
                <p className="text-xs text-gray-500">
                  ŸÑÿß ÿ™Ÿàÿ¨ÿØ ÿ®ŸäÿßŸÜÿßÿ™ ŸÑÿπÿ±ÿ∂ ÿØŸÅÿ™ÿ± ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖŸäŸÜ‚Ä¶
                </p>
              )}
            </div>
          </div>
        </div>

        {/* ÿßŸÑŸÅŸÑÿßÿ™ÿ± */}
        <div className="p-4 bg-white border rounded-lg shadow-sm">
          <h2 className="mb-3 text-sm font-semibold text-gray-700">
            üîç ŸÅŸÑÿ™ÿ±ÿ© ÿßŸÑÿπŸÖŸÑŸäÿßÿ™
          </h2>
          <div className="grid grid-cols-1 gap-3 md:grid-cols-6">
            <input
              type="text"
              placeholder="ÿ®ÿ≠ÿ´: ŸÖÿ±ÿ¨ÿπ / ŸàÿµŸÅ / ŸÖÿ≥ÿ™ÿÆÿØŸÖ"
              className="px-3 py-2 text-sm border rounded-md focus:ring-2 focus:ring-sky-400"
              value={search}
              onChange={(e) => setSearch(e.target.value)}
            />

            <select
              value={typeFilter}
              onChange={(e) => setTypeFilter(e.target.value)}
              className="px-3 py-2 text-sm border rounded-md"
            >
              <option value="all">ŸÉŸÑ ÿßŸÑÿ£ŸÜŸàÿßÿπ</option>
              <option value="ŸÅÿßÿ™Ÿàÿ±ÿ© ÿ®Ÿäÿπ">ŸÅÿßÿ™Ÿàÿ±ÿ© ÿ®Ÿäÿπ</option>
              <option value="ÿ•Ÿäÿ±ÿßÿØ ÿ¢ÿÆÿ±">ÿ•Ÿäÿ±ÿßÿØ ÿ¢ÿÆÿ±</option>
              <option value="ŸÖÿµÿ±ŸàŸÅ ŸÖÿ¥ÿ™ÿ±Ÿäÿßÿ™">ŸÖÿµÿ±ŸàŸÅ ŸÖÿ¥ÿ™ÿ±Ÿäÿßÿ™</option>
              <option value="ŸÖÿµÿ±ŸàŸÅ ÿ™ÿ¥ÿ∫ŸäŸÑŸä">ŸÖÿµÿ±ŸàŸÅ ÿ™ÿ¥ÿ∫ŸäŸÑŸä</option>
              <option value="ŸÖÿ±ÿ™ÿ¨ÿπ ÿπŸÖŸäŸÑ">ŸÖÿ±ÿ™ÿ¨ÿπ ÿπŸÖŸäŸÑ</option>
            </select>

            <select
              value={directionFilter}
              onChange={(e) => setDirectionFilter(e.target.value)}
              className="px-3 py-2 text-sm border rounded-md"
            >
              <option value="all">ÿ•Ÿäÿ±ÿßÿØ + ŸÖÿµÿ±ŸàŸÅ</option>
              <option value="in">ÿ•Ÿäÿ±ÿßÿØÿßÿ™ ŸÅŸÇÿ∑</option>
              <option value="out">ŸÖÿµÿ±ŸàŸÅÿßÿ™ ŸÅŸÇÿ∑</option>
            </select>

            <select
              value={userFilter}
              onChange={(e) => setUserFilter(e.target.value)}
              className="px-3 py-2 text-sm border rounded-md"
            >
              <option value="all">ŸÉŸÑ ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖŸäŸÜ</option>
              {users.map((u) => (
                <option key={u.id} value={u.id}>
                  {u.name}
                </option>
              ))}
            </select>

            <input
              type="date"
              value={dateFrom}
              onChange={(e) => setDateFrom(e.target.value)}
              className="px-3 py-2 text-sm border rounded-md"
            />
            <input
              type="date"
              value={dateTo}
              onChange={(e) => setDateTo(e.target.value)}
              className="px-3 py-2 text-sm border rounded-md"
            />
          </div>
        </div>

        {/* ÿ¨ÿØŸàŸÑ ÿßŸÑÿπŸÖŸÑŸäÿßÿ™ */}
        <div className="p-4 overflow-x-auto bg-white border rounded-lg shadow-sm">
          <h2 className="mb-3 text-sm font-semibold text-gray-700">
            üìã ÿßŸÑÿπŸÖŸÑŸäÿßÿ™ ÿßŸÑŸÖÿßŸÑŸäÿ©
          </h2>
          <table className="w-full min-w-[980px] text-sm text-right">
            <thead className="text-gray-700 bg-gray-50">
              <tr>
                <th className="px-3 py-2">ŸÖÿ±ÿ¨ÿπ</th>
                <th className="px-3 py-2">ÿßŸÑŸÜŸàÿπ</th>
                <th className="px-3 py-2">ÿßÿ™ÿ¨ÿßŸá</th>
                <th className="px-3 py-2">ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖ</th>
                <th className="px-3 py-2">ÿßŸÑÿ™ÿµŸÜŸäŸÅ</th>
                <th className="px-3 py-2">ÿ∑ÿ±ŸäŸÇÿ© ÿßŸÑÿØŸÅÿπ</th>
                <th className="px-3 py-2">ÿßŸÑÿ™ÿßÿ±ŸäÿÆ</th>
                <th className="px-3 py-2">ÿßŸÑŸÖÿ®ŸÑÿ∫</th>
              </tr>
            </thead>
            <tbody>
              {filteredTransactions.length ? (
                filteredTransactions.map((t) => (
                  <tr
                    key={t.id}
                    className="transition border-t hover:bg-gray-50"
                  >
                    <td className="px-3 py-2 text-xs sm:text-sm">
                      {t.refCode || "‚Äî"}
                    </td>
                    <td className="px-3 py-2 text-xs sm:text-sm">
                      {t.type}
                    </td>
                    <td className="px-3 py-2 text-xs sm:text-sm">
                      {t.direction === "in" ? "ÿ•Ÿäÿ±ÿßÿØ" : "ŸÖÿµÿ±ŸàŸÅ"}
                    </td>
                    <td className="px-3 py-2 text-xs sm:text-sm">
                      {t.userName || "‚Äî"}
                    </td>
                    <td className="px-3 py-2 text-xs sm:text-sm">
                      {t.category || "‚Äî"}
                    </td>
                    <td className="px-3 py-2 text-xs sm:text-sm">
                      {t.paymentMethod || "‚Äî"}
                    </td>
                    <td className="px-3 py-2 text-xs sm:text-sm">
                      {t.date.replace("T", " ").slice(0, 16)}
                    </td>
                    <td className="px-3 py-2 text-xs font-semibold text-emerald-700 sm:text-sm">
                      {formatCurrency(t.amount)}
                    </td>
                  </tr>
                ))
              ) : (
                <tr>
                  <td
                    colSpan={8}
                    className="py-5 text-sm text-center text-gray-500"
                  >
                    ŸÑÿß ÿ™Ÿàÿ¨ÿØ ÿπŸÖŸÑŸäÿßÿ™ ŸÖÿ∑ÿßÿ®ŸÇÿ© ŸÑŸÑŸÅŸÑÿßÿ™ÿ± ÿßŸÑÿ≠ÿßŸÑŸäÿ©‚Ä¶
                  </td>
                </tr>
              )}
            </tbody>
          </table>
        </div>

        {/* ŸÖŸàÿØÿßŸÑ ÿ•ÿ∂ÿßŸÅÿ© ÿπŸÖŸÑŸäÿ© */}
        {showAddModal && (
          <Modal
            title="‚ûï ÿ•ÿ∂ÿßŸÅÿ© ÿπŸÖŸÑŸäÿ© ŸÖÿßŸÑŸäÿ©"
            onClose={() => setShowAddModal(false)}
            onConfirm={handleSaveNewTrans}
            confirmText="ÿ≠ŸÅÿ∏ ÿßŸÑÿπŸÖŸÑŸäÿ©"
            size="md"
          >
            <div className="space-y-3 text-sm">
              <div className="grid grid-cols-1 gap-3 sm:grid-cols-2">
                <div>
                  <label className="block mb-1 text-xs text-gray-600">
                    ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖ
                  </label>
                  <select
                    className="w-full px-3 py-2 text-sm border rounded-md"
                    value={newTrans.userId}
                    onChange={(e) =>
                      setNewTrans((prev) => ({
                        ...prev,
                        userId: e.target.value,
                      }))
                    }
                  >
                    <option value="">ÿßÿÆÿ™ÿ± ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖ</option>
                    {users.map((u) => (
                      <option key={u.id} value={u.id}>
                        {u.name} ‚Äî {u.role}
                      </option>
                    ))}
                  </select>
                </div>

                <div>
                  <label className="block mb-1 text-xs text-gray-600">
                    ŸÜŸàÿπ ÿßŸÑÿπŸÖŸÑŸäÿ©
                  </label>
                  <select
                    className="w-full px-3 py-2 text-sm border rounded-md"
                    value={newTrans.type}
                    onChange={(e) => {
                      const val = e.target.value;
                      let direction = "in";
                      if (
                        val === "ŸÖÿµÿ±ŸàŸÅ ŸÖÿ¥ÿ™ÿ±Ÿäÿßÿ™" ||
                        val === "ŸÖÿµÿ±ŸàŸÅ ÿ™ÿ¥ÿ∫ŸäŸÑŸä" ||
                        val === "ŸÖÿ±ÿ™ÿ¨ÿπ ÿπŸÖŸäŸÑ"
                      ) {
                        direction = "out";
                      }
                      setNewTrans((prev) => ({
                        ...prev,
                        type: val,
                        direction,
                      }));
                    }}
                  >
                    <option value="ŸÅÿßÿ™Ÿàÿ±ÿ© ÿ®Ÿäÿπ">ŸÅÿßÿ™Ÿàÿ±ÿ© ÿ®Ÿäÿπ</option>
                    <option value="ÿ•Ÿäÿ±ÿßÿØ ÿ¢ÿÆÿ±">ÿ•Ÿäÿ±ÿßÿØ ÿ¢ÿÆÿ±</option>
                    <option value="ŸÖÿµÿ±ŸàŸÅ ŸÖÿ¥ÿ™ÿ±Ÿäÿßÿ™">ŸÖÿµÿ±ŸàŸÅ ŸÖÿ¥ÿ™ÿ±Ÿäÿßÿ™</option>
                    <option value="ŸÖÿµÿ±ŸàŸÅ ÿ™ÿ¥ÿ∫ŸäŸÑŸä">ŸÖÿµÿ±ŸàŸÅ ÿ™ÿ¥ÿ∫ŸäŸÑŸä</option>
                    <option value="ŸÖÿ±ÿ™ÿ¨ÿπ ÿπŸÖŸäŸÑ">ŸÖÿ±ÿ™ÿ¨ÿπ ÿπŸÖŸäŸÑ</option>
                  </select>
                </div>
              </div>

              <div className="grid grid-cols-1 gap-3 sm:grid-cols-2">
                <div>
                  <label className="block mb-1 text-xs text-gray-600">
                    ÿßŸÑŸÖÿ®ŸÑÿ∫
                  </label>
                  <input
                    type="number"
                    className="w-full px-3 py-2 text-sm border rounded-md"
                    value={newTrans.amount}
                    onChange={(e) =>
                      setNewTrans((prev) => ({
                        ...prev,
                        amount: e.target.value,
                      }))
                    }
                  />
                </div>
                <div>
                  <label className="block mb-1 text-xs text-gray-600">
                    ÿßŸÑÿ™ÿßÿ±ŸäÿÆ ŸàÿßŸÑŸàŸÇÿ™
                  </label>
                  <input
                    type="datetime-local"
                    className="w-full px-3 py-2 text-sm border rounded-md"
                    value={newTrans.date}
                    onChange={(e) =>
                      setNewTrans((prev) => ({
                        ...prev,
                        date: e.target.value,
                      }))
                    }
                  />
                </div>
              </div>

              <div className="grid grid-cols-1 gap-3 sm:grid-cols-2">
                <div>
                  <label className="block mb-1 text-xs text-gray-600">
                    ÿßŸÑÿ™ÿµŸÜŸäŸÅ
                  </label>
                  <input
                    type="text"
                    className="w-full px-3 py-2 text-sm border rounded-md"
                    placeholder="ŸÖÿ´ŸÑ: ŸÖÿ®Ÿäÿπÿßÿ™ / ŸÖÿ¥ÿ™ÿ±Ÿäÿßÿ™ / ÿ•Ÿäÿ¨ÿßÿ±..."
                    value={newTrans.category}
                    onChange={(e) =>
                      setNewTrans((prev) => ({
                        ...prev,
                        category: e.target.value,
                      }))
                    }
                  />
                </div>
                <div>
                  <label className="block mb-1 text-xs text-gray-600">
                    ÿ∑ÿ±ŸäŸÇÿ© ÿßŸÑÿØŸÅÿπ
                  </label>
                  <select
                    className="w-full px-3 py-2 text-sm border rounded-md"
                    value={newTrans.paymentMethod}
                    onChange={(e) =>
                      setNewTrans((prev) => ({
                        ...prev,
                        paymentMethod: e.target.value,
                      }))
                    }
                  >
                    <option value="cash">ŸÜŸÇÿØÿßŸã</option>
                    <option value="card">ÿ®ÿ∑ÿßŸÇÿ©</option>
                    <option value="bank">ÿ™ÿ≠ŸàŸäŸÑ ÿ®ŸÜŸÉŸä</option>
                    <option value="wallet">ŸÖÿ≠ŸÅÿ∏ÿ©</option>
                  </select>
                </div>
              </div>

              <div>
                <label className="block mb-1 text-xs text-gray-600">
                  ŸÖÿ±ÿ¨ÿπ (ÿßÿÆÿ™Ÿäÿßÿ±Ÿä)
                </label>
                <input
                  type="text"
                  className="w-full px-3 py-2 text-sm border rounded-md"
                  placeholder="ŸÖÿ´ŸÑ: INV-2025-001 ÿ£Ÿà EXP-PO-01"
                  value={newTrans.refCode}
                  onChange={(e) =>
                    setNewTrans((prev) => ({
                      ...prev,
                      refCode: e.target.value,
                    }))
                  }
                />
              </div>

              <div>
                <label className="block mb-1 text-xs text-gray-600">
                  ŸàÿµŸÅ / ŸÖŸÑÿßÿ≠ÿ∏ÿ©
                </label>
                <textarea
                  rows={3}
                  className="w-full px-3 py-2 text-sm border rounded-md"
                  placeholder="ŸàÿµŸÅ ŸÖÿÆÿ™ÿµÿ± ŸÑŸÑÿπŸÖŸÑŸäÿ© ÿßŸÑŸÖÿßŸÑŸäÿ©"
                  value={newTrans.description}
                  onChange={(e) =>
                    setNewTrans((prev) => ({
                      ...prev,
                      description: e.target.value,
                    }))
                  }
                />
              </div>
            </div>
          </Modal>
        )}
      </div>
    </Layout>
  );
}

// ÿ®ÿ∑ÿßŸÇÿ© ŸÖŸÑÿÆÿµ
function SummaryCard({ title, value, color }) {
  return (
    <div className="p-4 text-center bg-white border rounded-lg shadow-sm">
      <p className="text-xs text-gray-500">{title}</p>
      <p className={`mt-1 text-lg sm:text-xl font-bold ${color}`}>{value}</p>
    </div>
  );
}








